<template>
    <div>
        <slot test="test" v-if="onlyBeers" :beers="beers"  />
        <slot v-else :checkins="checkins" />
    </div>
</template>

<script>
import {
  getLatestCheckIns,
  checkinSubscription,
} from '../../database/queries.js'
import { map, prop, dropLast } from 'ramda'
import gql from 'graphql-tag'

const dropTail = dropLast(1)

export default {
  data() {
    return {
      checkins: [],
    }
  },
  apollo: {
    checkins: {
      query: getLatestCheckIns,
      fetchPolicy: 'cache-and-network',
      variables() {
        return {
          limit: this.limit,
          onlyBeers: this.onlyBeers,
        }
      },
      error(err) {
        console.log(err)
      },
      subscribeToMore: {
        document: checkinSubscription,
        updateQuery: (
          { checkins },
          { subscriptionData: { data: { checkinAdded } } }
        ) => ({
          checkins: [checkinAdded, ...dropTail(checkins)],
        }),
        variables() {
          return {
            onlyBeers: this.onlyBeers,
          }
        },
        error(err) {
          console.log(err)
        },
      },
    },
  },
  computed: {
    beers() {
      return map(prop('beer'))(this.checkins)
    },
  },
  props: {
    limit: {
      type: Number,
      required: false,
      default: Number.MAX_SAFE_INTEGER,
    },
    onlyBeers: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
}
</script>

